// 
export function fetch_age_bindConsumer(url, data) {  // 
  var postData = JSON.stringify(data);
  var status = data.status;
  $.ajax({
    url: url,
    data: postData,
    success: function(res) {
      if (res.code == '200') {
        let oldMes
        if (status == '0') {
          oldMes = document.querySelector('div.uk-alert');
          fn_remMes(oldMes);
          UIkit.modal.alert('').then(function() {
            location.reload();
          });
        }
        if (status == '1') {
          oldMes = document.querySelector('div.uk-alert');
          fn_remMes(oldMes);
          UIkit.modal.alert('').then(function() {
            location.reload();
          });
        }
      } else {
        var mes = fn_getMes('/', {style: 'danger', close: true});
        $(mes).insertBefore(document.querySelector('form'));
      }
    }
  });
}

